<template>
  <div
    ref="tooltip"
    class="custom-tooltip"
    v-show="visible"
    v-html="content"
  ></div>
</template>

<script>
export default {
  name: 'CustomTooltip',
  props: {
    visible: { type: Boolean, default: false },
    content: { type: String, default: '' },
    target: { type: [Object, null], default: null }
  },
  watch: {
    visible(val) {
      if (val) this.$nextTick(this.setPosition);
    },
    target() {
      if (this.visible) this.$nextTick(this.setPosition);
    }
  },
  methods: {
    setPosition() {
      if (!this.target || !this.$refs.tooltip) return;
      const tooltip = this.$refs.tooltip;
      const cellRect = this.target.getBoundingClientRect();
      const tooltipRect = tooltip.getBoundingClientRect();
      let left = cellRect.left + (cellRect.width - tooltipRect.width) / 2;
      let top = cellRect.top - tooltipRect.height - 8 - 200; //  -200  
      if (left < 2) left = 2;
      if (left + tooltipRect.width > window.innerWidth - 2) {
        left = window.innerWidth - tooltipRect.width - 2;
      }
      tooltip.style.left = left + 'px';
      tooltip.style.top = top + 'px';
    }
  }
};
</script>

<style scoped>
.custom-tooltip {
  position: fixed;
  background: #232527;
  color: #fff;
  border-radius: 10px;
  padding: 14px 18px;
  font-size: 14px;
  z-index: 9999;
  box-shadow: 0 4px 16px rgba(0,0,0,0.25);
  pointer-events: none;
  min-width: 160px;
  max-width: 240px;
  transition: opacity 0.15s;
  display: block;
}
.tooltip-date { color: #1ed760; font-weight: bold; margin-bottom: 6px; }
.tooltip-value { color: #fff; }
</style> 